    // TODO: promise
    public connect(dbname: string, store: string, v: number = 1, indexes?: string[], unique: boolean = true): void {
        let self = this;

        self.dbname = dbname;
        self.storename = store;
        self.version = v;

        // 1. IDBFactory https://developer.mozilla.org/en-US/docs/Web/API/IDBFactory
        let indexedDB: IDBFactory =
            window.indexedDB ||
            window.webkitIndexedDB ||
            window.mozIndexedDB ||
            window.msIndexedDB;

        if (!indexedDB) {
            console.error('IndexedDB' + dbname);
            return;
        }

        // 2. IDBFactoryopenindexedDB
        //  IDBRequest,onerroronsuccess
        let openRequest: IDBOpenDBRequest = indexedDB.open(dbname, v);

        // 3. 
        // onsuccess
        openRequest.onsuccess = function (e) {
            self.db = (<IDBOpenDBRequest>e.target).result;
            console.warn("! database = " + dbname);
            // console.warn(e.target);
            // console.warn(db.objectStoreNames);
            // add();
            // ask();
            // readAll();
            // getData("settings2");
        };

        // 
        openRequest.onerror = function (e) {
            console.error('!  database = ' + dbname);
            console.error((<IDBOpenDBRequest>e.target).error!.message);
        };

        // 
        openRequest.onupgradeneeded = function (e) {
            self.db = (<IDBOpenDBRequest>e.target).result; //  demoDB IDBDatabase,
            console.warn(' database = ' + dbname);
            if (!self.db.objectStoreNames.contains(store)) {
                // keyPathautoIncrement,objectStore
                // objectStoreIDBObjectStore
                let objectStore = self.db.createObjectStore(store, {
                    keyPath: 'id',
                    autoIncrement: true
                });

                // unique IDBIndex
                if (indexes) {
                    indexes.forEach(item => {
                        objectStore.createIndex(item, item, {
                            unique: unique
                        });
                    });
                }
            }
            console.warn(' ' + self.db.version);
        };
    }

